import React from 'react';
import IntrestsData from '../../data/Intrests';
import KnowledgeTiles from './KnowledgeTiles'

const IntrestList = () => (
    <div style={{ maxWidth: '80%' }}>
        {IntrestsData.map((row) =>
            <KnowledgeTiles name={row} />
        )}
    </div>
);

export default IntrestList;