import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import workexperiance from '../../data/workexperiance';
import {
    CustomHeader,
    CustomHeadContent,
    ContentWrapper,
    ContentBody
} from '../StyleComponents/Styles'

import Intrests from './Intrests';
import Education from './Education'
import DownloadButton from '../DownloadButton';

const ExperianceBlock = () => (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Card style={{ width: '90%', marginTop: '5%', backgroundColor: 'gray', maxWidth: '1400px', minWidth: '100px' }}>
            <CardContent>
                <ContentWrapper>
                    <h1>Work Experience</h1>
                    <div className="experience-stepper">
                        {workexperiance.map((item) => (
                            <div key={item.id} className="step">
                                <div>
                                    <div className="circle" />
                                    <div className="line" />
                                </div>
                                <CustomHeader>
                                    <CustomHeadContent>
                                        <div>
                                            <h3>{item.position}</h3>
                                            <a
                                                href={item.link}
                                                target="_blank"
                                                rel="noreferrer"
                                            >
                                                {item.label}
                                            </a>
                                            <p>{item.date}</p>
                                        </div>
                                        <a href={item.companyLink}>
                                            <img
                                                src={item.icon}
                                                alt="company"
                                                width="175px"
                                            />
                                        </a>
                                    </CustomHeadContent>
                                    <ContentBody
                                        dangerouslySetInnerHTML={{
                                            __html: item.description,
                                        }}
                                    />
                                </CustomHeader>
                            </div>
                        ))}
                    </div>

                </ContentWrapper>
                <Education />
                <Intrests />
                <DownloadButton />
            </CardContent>
        </Card>
    </div>
);

export default ExperianceBlock;