import React from 'react';
import ProjectsData from '../../data/ProjectsData'
import ProjectTemplate from './ProjectTemplate'

import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

function Projects() {
    const [expanded, setExpanded] = React.useState('panel1');
    const handleChange = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

      return(
          <div style={{maxWidth: '90%'}}>
        <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} style={{marginBottom:'3%'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          style={{backgroundColor: "#212121"}}
        >
          <Typography style={{color:'whitesmoke'}}>Personal Projects</Typography>
        </AccordionSummary>
        <AccordionDetails style={{backgroundColor:'#1F1F1F'}}>
        {ProjectsData.map((row) =>
            {if(row.type === "Personal"){
                return(<ProjectTemplate project={row} />)
            }}
        )}
        </AccordionDetails>
      </Accordion>
        <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          style={{backgroundColor: "#212121"}}
        >
          <Typography style={{color:'whitesmoke'}}>School Projects</Typography>
        </AccordionSummary>
        <AccordionDetails style={{backgroundColor:'#1F1F1F'}}>
        {ProjectsData.map((row) =>
            {if(row.type === "School Assignment"){
                return(<ProjectTemplate project={row} />)
            }}
        )}
        </AccordionDetails>
      </Accordion>
        </div>
      );
}

const ProjectList = () => (
    <div style={{width:'95%', display: 'flex', justifyContent: 'center', marginTop: '5%'}}>
        <Projects/>
    </div>
);

export default ProjectList;