import React, { useEffect, useState } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { send } from 'emailjs-com';
import { useAlert } from "react-alert";
import WebNotification from 'react-web-notifications'

function App() {

    const initialState = {
        from_name: '',
        message: '',
        reply_to: '',
    }

    const [toSend, setToSend] = useState(initialState);
  
    const onSubmit = (e) => {
        e.preventDefault();
        
        if(toSend.from_name !== '' && toSend.message !== '' && toSend.reply_to !== ''){
            const confirmBox = window.confirm(
                "Are you sure you want to send this email?"
            )

            if (confirmBox === true){
                send(
                  'service_v3v4h2m',
                  'template_edkp7gm',
                  toSend,
                  'user_1m11a0ZtSCW56dkQj3aRK'
                ).then((response) => {
                    clearState();

                  }).catch((err) => {
                    console.log('FAILED...', err);
                  });
            }
        }
      };
  
    const handleChange = (e) => {
      setToSend({ ...toSend, [e.target.name]: e.target.value });
    };

    const clearState = () => {
        setToSend({ ...initialState });
    };
  
    return (
      <div style={{ display: 'flex', justifyContent: 'center', width: '100%' }}>
        <form onSubmit={onSubmit}>

            <div style={{display: 'block', width: '100%'}}>
            <div style={{display: 'inline-block'}}>
              <input
                type='text'
                name='from_name'
                placeholder='Your Name'
                value={toSend.from_name}
                onChange={handleChange}
                style={{width:'100%', height: '100%', paddingBottom:'3%', marginBottom:'5%'}}
              />
            </div>
            </div>


            <div style={{display: 'block', width: '100%'}}>
            <div style={{display: 'inline-block'}}>
              <input
                type='text'
                name='reply_to'
                placeholder='Your email'
                value={toSend.reply_to}
                onChange={handleChange}
                style={{width:'100%', height: '100%', paddingBottom:'3%', marginBottom:'5%'}}
              />
              </div>
              </div>

              <div style={{display: 'block', width: '100%'}}>
            <div style={{display: 'inline-block'}}>
              <input
                type='text'
                name='message'
                placeholder='Your message'
                value={toSend.message}
                onChange={handleChange}
                style={{width:'100%', height: '10em', paddingBottom:'3%', marginBottom:'5%'}}
              />
            </div>
            </div>
              
              <div style={{display: 'block', width: '100%'}}>
              <div style={{display: 'inline-block'}}>
              <button type='submit' style={{width:'100%'}}>Send Email Message</button>
              </div>
              </div>

            </form>
            </div>
           
    );
  }

const CardMailItem = () => (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Card style={{ display: 'flex', justifyContent: 'center', width: '90%', marginTop: '5%', backgroundColor: 'gray', maxWidth: '800px', minWidth: '100px' }}>
            <CardContent style={{ justifyContent: 'center', marginLeft: '5%' }}>
            <div style={{ display: 'flex', justifyContent: 'center' }}> 
            <h4>Contact Me!</h4>
            </div>
            <App></App>
            </CardContent>
        </Card>
    </div>
);

export default CardMailItem;
