import React from 'react';
import { Link } from 'react-router-dom';
import me from "../../logo.png"
import Socials from '../Socials';

const Footer = () => (
    <div style={{ marginTop: '10%', marginLeft: '3%' }}>
        <section id="sidebar">
            <section id="intro"></section>
            <Link to="/" className="logo">
                <img src={me} alt="me" style={{ maxWidth: "7%" }} />
            </Link>
            <header>
                <h3 style={{ color: 'ghostwhite', fontWeight: 'bold' }}>Jordan Wootton</h3>
            </header>
            <section id="footer">
                <h7 className="copyright" style={{ color: 'ghostwhite' }}>&copy; Jordan Wootton <a href="mailto:contact@jordanwootton.ca">contact@jordanwootton.ca</a></h7>
            </section>
            <section id="footer" style={{ paddingBottom: '2%', marginTop: '5%', marginLeft: '7%' }}>
                <Socials />
            </section>
        </section>
    </div>
);

export default Footer;
