import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';

import {
    CustomHeader,
    CustomHeadContent,
    ContentWrapper,
    ContentBody
} from '../StyleComponents/Styles'


const ProjectTemplate = (props) => (
    <div style={{ display: 'flex', justifyContent: 'center' }}>
        <Card style={{ width: '95%', marginTop: '5%', backgroundColor: 'gray', maxWidth: '800px', minWidth: '100px' }}>
            <CardContent>
                <ContentWrapper>
                    <div className="experience-stepper">
                        <CustomHeader>
                            <CustomHeadContent>
                                <div>
                                    <h1>{props.project.label}</h1>
                                    <h3>{props.project.type}</h3>
                                    <p>{props.project.date}</p>
                                </div>
                                <a href={props.project.link} target="_blank">
                                    <img
                                        src={props.project.icon}
                                        alt="company"
                                        width="175px"
                                    />
                                </a>
                            </CustomHeadContent>
                            <ContentBody
                                dangerouslySetInnerHTML={{
                                    __html: props.project.description,
                                }}
                            />
                        </CustomHeader>
                    </div>
                </ContentWrapper>
            </CardContent>
        </Card>
    </div>
);

export default ProjectTemplate;