import React from 'react';
import JW_Resume from '../assests/file_downloads/JW_Resume.pdf'
import downloadbtnImg from '../assests/buttons/download_button.png'
import {
    CustomHeader,
    CustomHeadContent,
    ContentWrapper,
    ContentBody
} from '../Components/StyleComponents/Styles'
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const DownloadButton = () => (
    <CustomHeader style={{marginTop: '2%'}}>
        <ContentWrapper>
            <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="download-resume"
          style={{backgroundColor: "#212121"}}
        >
          <Typography style={{color: 'whitesmoke'}}>Download Resume</Typography>
        </AccordionSummary>
        <AccordionDetails style={{backgroundColor:'#1F1F1F'}}>
          <Typography>
          <div style={{ justifyContent: 'center', display: 'flex' }}>
                <a href={JW_Resume} download target="_blank" style={{ textDecoration: "none", justifyContent: 'center', display: 'flex', maxWidth: '10%' }}>
                    <img src={downloadbtnImg} style={{ maxWidth: '100%' }} />
                </a>
            </div>
          </Typography>
        </AccordionDetails>
      </Accordion>

            
        </ContentWrapper>
    </CustomHeader >
);

export default DownloadButton;