import React from 'react';
import {
    CustomHeader,
    CustomHeadContent,
    ContentWrapper,
    ContentBody
} from '../StyleComponents/Styles'

import EducationData from '../../data/Education';

const Education = () => (
    <CustomHeader>
        <ContentWrapper>
            <h1>Education</h1>
            <div className="experience-stepper">
                {EducationData.map((item) => (
                    <div key={item.id} className="step">
                        <div>
                            <div className="circle" />
                            <div className="line" />
                        </div>
                        <CustomHeader>
                            <CustomHeadContent>
                                <div>
                                    <h3>{item.position}</h3>
                                    <a
                                        href={item.link}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        {item.label}
                                    </a>
                                    <p>{item.date}</p>
                                </div>
                                <a href={item.companyLink}>
                                    <img
                                        src={item.icon}
                                        alt="company"
                                        width="175px"
                                    />
                                </a>
                            </CustomHeadContent>
                            <ContentBody
                                dangerouslySetInnerHTML={{
                                    __html: item.description,
                                }}
                            />
                        </CustomHeader>
                    </div>
                ))}
            </div>
        </ContentWrapper>
    </CustomHeader >
);

export default Education;