import React from 'react';
import { Link } from 'react-router-dom';
import routes from '../../data/routes';

// Websites Navbar, displays routes defined in 'src/data/routes'
const NavItemVerticle = () => (
    <div style={{ marginLeft: "10%", marginTop: "1.25%" }}/*Links*/>
        {routes.filter((l) => !l.index).map((l) => (
            <Link to={l.path} style={{ marginRight: "15%", color: '#B7B8B6', textDecoration: 'none', fontSize: 20, display: 'flex', marginTop: '30%' }}>{l.label}</Link>
        ))}
    </div>
);

export default NavItemVerticle;